import loading from '../../components/loadding'
import { mapState } from 'vuex'
export default {
    components: {
        loading
    },
    computed: {
        ...mapState(['userInfo'])
    },
    data() {
        return {
            goodsId: '',
            goodsObj: null
        }
    },
    created() {
        this.$nextTick(() => {
            this.getGoodsDetail()
        })
    },
    onLoad(option){
        this.goodsId = option.id
    },
    methods: {
        async getGoodsDetail() {
            const res = await this.$api.getGoodsDetail(this.goodsId)
            if (res.code === '0') {
                this.goodsObj = res.data
            } else {
                this.$tip.toast(res.massege,'none')
            }
        },
        gotoPayPage () {
            let hasLogin = !!this.userInfo
            if (!hasLogin) {    
                this.$tip.toast('请先登录','none')          
                uni.navigateTo({
                    url: '../login/login',
                });
                return
            }
            uni.navigateTo({
                url: `../payGoodsPage/payGoodsPage?id=${this.goodsObj.id}`
            })
        }
    },
}
